@import './../../../assets/abstracts/mixins';
@import './../../../assets/abstracts/vars';

.card{
  border: 2px solid white;

  text-align: center;

  position: relative;


  overflow: hidden;


  .projectCardTitle{
    //margin-top: 10px;

    //max-width: 100%;
    margin: 0 auto 0;

    font-size: 1.25rem;
    line-height: 40px;

    transition: all .3s ease-in-out;

    @media screen and (max-width: 1180px) {
      transform: translateX(0);

    }
  }
  &:hover .projectCardTitle{
    visibility: hidden;
  }
  .cardImage{
    width: 430px;
    height: 330px;

    object-fit: cover;

    transition: all .3s ease-in-out;

    @media screen and (max-width: 1520px) {
      width: 530px;
      height: 380px;

    }
    @media screen and (max-width: 600px) {
      width: 450px;
      height: 320px;
    }
    @media screen and (max-width: 490px) {
      width: 370px;
      height: 270px;
    }
    @media screen and (max-width: 400px) {
      width: 290px;
      height: 220px;
    }
  }
  &:hover .cardImage{
    transform: scale(1.3);
  }
  .cardLink{
    display: block;

    width: 164px;
    height: 54px;

    position: absolute;
    top: 90%;
    left: 50%;

    @include text-edit(Poppins, 600, 1.4rem, 50px);
    text-transform: none;
    text-decoration: none;
    text-align: center;

    background-color: $darkThemeColor;
    color: $lightThemeColor;

    transform: translate(-50%, -50%);

    visibility: hidden;

    transition: all .3s ease-in-out;
  }
  &:hover .cardLink{
    visibility: visible;


  }
}